let lastUrl = '';
let secondlastUrl = null;

export const authSuccess = () => {
    return {
        type: actionsType.AUTH_LOGIN
    };
};
export const authFail = (error) => {
    return {
        type: actionsType.AUTH_FAIL,
        error: error
    };
};

export const authLogout = () => {
    return {
        type: actionsType.AUTH_LOGOUT
    };
};

export const checkAuthTimeout = (expirationTime) => {
    return dispatch => {
        setTimeout(() =>{
            dispatch(authLogout());
        },expirationTime*1000);
    };
};
export const updateDataFirebase = (nodeName,id,data,input) => { // nodeName = firebase Node Name
    return async dispatch => {
        let combineArr = [];
        dispatch(changeLoadingToTrue());
        let userRef = database.ref(`${nodeName}/${id}`);
        await uploadMoreImages(input).then(arr => combineArr = [...arr,...data.url]);
        await userRef.update({...data,url:combineArr});
        await dispatch(downloadData());
    };
};
const uploadMoreImages = (input) => { // func for assigning more img to 1 db element
    return new Promise(async (resolve, reject) => {
        let fotoArr = [];
        const file = document.getElementById(input);
        if(file.files.length === 0){
            resolve(fotoArr);
        };
        for(let i=0;i<file.files.length;i++){
             const fileRef = storageRef.child('images/' + file.files[i].name)
             const uploadTaskSnapshot = await fileRef.put(file.files[i]);
             const downloadURL = await uploadTaskSnapshot.ref.getDownloadURL();
             fotoArr.push(downloadURL);
             if(i===file.files.length-1){
                 resolve(fotoArr);
             };
        };
    });
};
export const uploadImage = (input,url,data) => {
    return async dispatch => {
        dispatch(changeLoadingToTrue());
        if(input === 'input_products--photo' || input === 'input_ourwork--photo'){
            await uploadMoreImages(input)
            .then((fotoArr) => dispatch(uploadDataFirebase(url,{url:fotoArr,name:data.name,...data})))
        }else{
            const file = document.getElementById(input);
            for(let i=0;i<file.files.length;i++){
                 const uploadTask = storageRef.child('images/' + file.files[i].name).put(file.files[i]);
                  uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED,()=>{},()=>{},
                    () => {
                    uploadTask.snapshot.ref.getDownloadURL().then((downloadURL) => {
                        dispatch(uploadDataFirebase(url,{url:downloadURL,name:file.files[i].name,...data}));
                    });
                });
            };
        };
    };
};

export const updateSuccess = (data,data2) => {
    return {
        type: actionsType.UPLOAD,
        data: data,
        data2: data2
    };
};

export const removeDataItem = (key) => {
    return {
        type: actionsType.SLIDER_REMOVEITEM,
        key:key
    };
};
export const changeLoadingToFalse = () => {
    return {
        type: actionsType.LOADING_FALSE
    };
};
export const changeLoadingToTrue = () => {
    return {
        type: actionsType.LOADING_TRUE
    };
};
export const removeFromFirebase = (url) => {
    return dispatch => {
        dispatch(changeLoadingToTrue());
        axios.delete(url)
        .then(response => {
            dispatch(downloadData());
        })
        .catch(err => console.log(err));
        
    };
};

export const uploadDataFirebase = (url,dataInfo) => {
    return  dispatch => {
        const data = {
            ...dataInfo
          };
          axios.post(url,data)
          .then(response => {
            dispatch(downloadData());
          })
          .catch(err => console.log(err));
        
    };
};

export const downloadData = (url = lastUrl,optionalURL = secondlastUrl) => {
    return dispatch => {
        dispatch(changeLoadingToFalse());
        const arr = [];
        const arr2 = [];
        lastUrl = url;
        secondlastUrl = optionalURL;
        axios.get(url)
        .then(res => {
            for (const [key, value] of Object.entries(res.data)) {
                arr.push([key, value]);
            } 
            !optionalURL && dispatch(updateSuccess(arr)); 
        }).then(() => { // optional data fetching
            if(optionalURL){
                axios.get(optionalURL)
                .then(res => {
                    for (const [key, value] of Object.entries(res.data)) {
                        arr2.push([key, value]);
                    } 
                    dispatch(updateSuccess(arr,arr2))
                });
            };
        });
    };
};
export const loginToDataBase = (action) => {
    return dispatch => {
        let url = 'https://identitytoolkit.googleapis.com/v1/accounts:signInWithPassword?key=AIzaSyBf5x6KDtgTfIurkIgqMIg5_wFIzTrUquI';
        const authData = {
            email: action.login,
            password: action.password,
            returnSecureToken: true
        };
        axios.post(url, authData)
            .then(response => {
                dispatch(authSuccess());
                dispatch(checkAuthTimeout(response.data.expiresIn));
            })
            .catch(err => {
                console.log(err.response.data.error);
                dispatch(authFail(err.response.data.error));
            });
    };
};
